// import { getOwnPropertySymbols } from 'core-js/core/object';
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        posts: [],
        selectedPost: null

    },
    getters: {

    },
    mutations: {
        setPosts(state,posts){
            state.posts = posts;
        },
        setPost(state, post){
            state.selectedPosts = post
        }

    },
    actions: {
        async getPosts({ commit }) {
           try {
              fetch('https://serene-crag-93594.herokuapp.com/api/posts')
                 .then(response => response.json())
                 .then(data => commit('setPosts', data.data));
           } catch (error) {
              commit('setPosts', []);
           }
        },
        async getPostById({ commit }, id) {
            try {
               fetch(`https://serene-crag-93594.herokuapp.com/api/posts/${id}`)
                  .then(response => response.json())
                  .then(data => commit('setPost', data.data));
            } catch (error) {
               commit('setPost', null);
            }
         },
         async updatePost({commit},id, data){
            try {
               fetch(`https://serene-crag-93594.herokuapp.com/api/posts/${id}`, {
                  method: 'post',
                  headers: {
                    "Content-type": "application/json"
                  },
                  body: data
                })
                .then(function (data) {
                  commit('setPost',data.data)
                })
                .catch(function (error) {
                  console.log('Request failed', error);
                });
            }
            catch (error){
               commit('setPost',null)
            }
         }
    
     }
   
})